<template>
  <section class="login">
    <Input
      class="login__username"
      :placeholder="t('signIn.username')"
      :opacity="true"
    >
      <template #prepend>
        <Icon class="login__username__icon" name="username" />
      </template>
    </Input>
    <Input
      class="login__password"
      :placeholder="t('signIn.password')"
      :opacity="true"
    >
      <template #prepend>
        <Icon class="login__password__icon" name="password" />
      </template>
    </Input>
    <p v-if="error" class="login__errorText" >{{ t('signIn.errorMessage') }}</p>
    <Button
      class="login__btn"
      type="primary"
      @click="toAuthentication"
    >{{ t('signIn.signInButton') }}</Button>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import Icon from '@/components/Icon/Icon'
import Input from '@/components/UIKit/Input'
import Button from '@/components/UIKit/Button'
import { ref } from 'vue'

export default {
  name: 'Login',
  components: { Icon, Input, Button },
  setup (_, { emit }) {
    const { t } = useI18n()
    const error = ref(true)
    const toAuthentication = () => {
      emit('toAuthentication', 'Authentication')
    }
    return {
      t,
      toAuthentication,
      error
    }
  }
}
</script>

