<template>
  <section class="authentication">
    <p class="authentication__text">{{ t('authentication.twoFactor') }}</p>
    <p class="authentication__sub-text">{{ t('authentication.subText', { app: authenticationApp || '' }) }}</p>
      <InputCode class="authentication__input" />
    <p v-if="error" class="authentication__error-message">{{ t('authentication.invalidCode') }}</p>
    <div class="authentication__group">
      <Button
        class="authentication__group-btn"
        type="primary"
      >{{ t('authentication.verifyBtn') }}</Button>
      <Button
        class="authentication__group-btn"
        @click="goBack"
      >{{ t('authentication.backBtn') }}</Button>
    </div>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref } from 'vue'
import Button from '@/components/UIKit/Button'
import InputCode from '@/components/UIKit/InputCode'

export default {
  name: 'Authentication',
  components: { Button, InputCode },
  setup (_, { emit }) {
    const { t } = useI18n()

    const authenticationApp = ref('2FA')
    const error = ref(true)

    const goBack = () => {
      emit('toLogin', 'Login')
    }
    return {
      t,
      authenticationApp,
      error,
      goBack
    }
  }
}
</script>

