<template>
  <section class="sign-in">
    <main class="sign-in-container">
      <div class="sign-in__lang">
        <Lang />
      </div>
      <div class="sign-in__logo">
        <Icon
          class="sign-in__logo-icon"
          name="logo-icon"
        />
      </div>
      <keep-alive>
        <component :is="activeComponent" @toAuthentication="changeComponent" @toLogin="changeComponent" ></component>
      </keep-alive>
    </main>
    <div class="sign-in__info">
      <Icon
        class="sign-in__icon__access"
        name="access"
      />
      <div class="sign-in__info__text">
        {{ t('signIn.access') }}
      </div>
    </div>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref } from 'vue'
import Icon from '@/components/Icon/Icon'
import Lang from '@/components/UIKit/Lang'
import Login from '@/views/admin/Login'
import Authentication from '@/views/admin/Authentication'

export default {
  name: 'SignIn',
  components: { Icon, Lang, Login, Authentication },
  setup () {
    const { t } = useI18n()
    const activeComponent = ref('Login')

    const changeComponent = (component) => {
      activeComponent.value = component
    }

    return {
      t,
      activeComponent,
      changeComponent
    }
  }
}
</script>

